import { actionTypes } from './actions';

const baseState = {
  loading: false,
  groups: [],
  group: {}
}

export const groupsReducer = (state = baseState, action) => {
  switch (action.type) {

    case actionTypes.SET_GROUPS: {
      return {
        ...state,
        groups: [...state.groups, ...action.payload]
      }
    }

    case actionTypes.SET_GROUPS_LOADING: {
      return {
        ...state,
        loading: action.payload
      }
    }
  
    default: {
      return state
    }
  }
}