import styled from 'styled-components';
import { groupsMock, mapStateToProps, mapActionsToProps } from './Groups.index';
import { GroupItem } from 'components';
import { useEffect } from 'react';
import { connect } from 'react-redux';

const Wrapper = styled.div`
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 24px;
`


const Component = ({ groups, loading, getGroups }) => {

  useEffect(() => {
    getGroups();
  }, [])

  const data = (!groups.length && !loading) ? groupsMock : groups;

  return <Wrapper>
    {data.map(group => <GroupItem key={group.id} data={group} />)}
  </Wrapper>
}

export const GroupsPage = connect(mapStateToProps, mapActionsToProps)(Component);